.d-folder-open, .d-folder-closed {
  display: inline-block;
  vertical-align: middle;
}
.d-folder-open:before, .d-folder-closed:before {
  font-size: 1.25rem;
  line-height: 1;
  margin-right: 0.25rem;
  display: inline-block;
}

.d-folder-open:before {
  content: "\e00e";
}

.d-folder-closed:before {
  content: "\e00d";
}

code {
  border-radius: 0.25rem;
  margin-right: 0.25rem;
}

html,
body {
  scroll-behavior: smooth;
  width: 100%;
  -webkit-font-smoothing: subpixel-antialiased;
  overflow-x: hidden;
}

.documentation-section {
  padding-bottom: 4rem;
}